%heading {
  font-family: $font-display;
  font-weight: 400;
}

.heading-1 {
  font-weight: 700;
  font-size: 4.25vw;
  color: $color-primary-dark;
  line-height: 1;
  font-family: $font-primary;
  text-align: justify;
  top:50%;
  transform: translate(5%, 50%);
}

.heading-2 {
  @extend %heading;
  font-size: 20px;
  font-style: italic;
  line-height: 2;
  padding: 0 14%;
  transform: translateY(-50%);

  &--light { color: $color-grey-light-1; }
  &--dark { color: $color-grey-dark-1; }
}

.heading-3 {
  @extend %heading;
  font-size: 1.6rem;
  color: $color-primary;
  text-transform: uppercase;
}

.heading-4 {
  @extend %heading;
  font-size: 1.9rem;

  &--light { color: $color-grey-light-1; }
  &--dark { color: $color-grey-dark-1; }
}

.btn {
  background-color: $color-primary;
  color: #fff;
  border: none;
  border-radius: 0;
  font-family: $font-display;
  font-size: 1.5rem;
  text-transform: uppercase;
  padding: 1.8rem 3rem;
  cursor: pointer;
  transition: all .2s;

  &:hover {
      background-color: $color-primary-dark;
  }
}

.mb-sm { margin-bottom: 2rem; }
.mb-md { margin-bottom: 3rem; }
.mb-lg { margin-bottom: 4rem; }
.mb-hg { margin-bottom: 8rem; }